<template>
  <div class="menu">
    <el-menu background-color="#383636" text-color="#fff" :default-active="active" unique-opened :collapse="iscollapse" router>
      <el-menu-item :index="item.path" v-for="item in menu" :key="item.id">
        <div slot="title" v-if="item.role === user.role_id.role_Id">
          <!-- <i :class="icon[item.id]" /> -->
          <span slot="title">{{ item.item }}</span>
        </div>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: "/user",
      iscollapse: false,
      user: JSON.parse(window.sessionStorage.user),
      menu: [
        { id: 1, path: "/userCenter/msg", item: "信息", role: 3 },
        { id: 2, path: "/userCenter/order", item: "订单", role: 3 },
        { id: 3, path: "/userCenter/goods", item: "产品", role: 2 },
        { id: 4, path: "/userCenter/order-shop", item: "订单", role: 2 },
        { id: 5, path: "/userCenter/afterSales", item: "售后", role: 2 },
        { id: 5, path: "/userCenter/statistics", item: "统计", role: 2 }
      ]
    }
  },
  created() {
    // 根据当前地址改变菜单选中状态
    this.active = this.$route.path
  },
  mounted() {},
  methods: {}
}
</script>

<style lang="less" scoped>
.menu {
  height: 450px;
}
.el-menu {
  height: 100%;
  border-right: none;
  background-color: #bfedf79c;
  a {
    text-decoration: none;
  }
}
.iconfont {
  font-size: 30px;
  margin-right: 10px;
}
.glyphicon {
